.carousel a {
  display: block;
  &:not(:first-child) {
    display: none;
  }
}
.slider-caption{
  position: absolute;
  bottom:30px;
  left: 90px;
  margin-bottom: 0;
  color: #fff;
  max-width: 340px;
}

.slick-slide:not(.slick-current) .img-carousel.lazyload {
  display: none!important;
}
.slick-slide figure{
  position: absolute;
  top: 0;
}

